.wrapper {
    width: calc(100vw - 1px);
    height: 100vh;
}

.sliderWrapper {
    position: absolute;
    bottom: 45px;
    width: 100vw;
    padding: 8px 24px;
}

.legend {
    position: fixed;
    top: 20px;
    left: 20px;
    width: 90px;
    z-index: 10;
}